<template>
  <div class="social page">
    <div class="social-card">
      <h2 class="text">{{language.social_title}}</h2>
      <p>{{language.social_tip1}}</p>
      <div class="social-tab-box flex flex-wrap">
        <div class="social-tab flex flex-column ai-center jc-center">
          <img class="social-tab-icon" :src="telegram.icon" alt="">
          <p class="text">{{telegram.title}}</p>
          <div class="social-tab-active hdr-el-blue"></div>
        </div>
        <a :href="item.link" target="_blank" class="social-tab flex flex-column ai-center jc-center" :key="item.name" v-for="item in list">
          <img class="social-tab-icon" :src="item.icon" alt="">
          <p class="text">{{item.title}}</p>
        </a>
      </div>
    </div>
    <div class="social-content social-card">
      <h2 class="text">{{language[renderCtx.title]}}</h2>
      <p>{{language[renderCtx.tips]}}</p>
      <div v-show="renderCtx.title" class="flex social-content-lang jc-between ai-center">
        <LocaleBar @change="changeHandle" :value="localVal" hasAll />
        <!-- <img class="social-content-lang-arrow" src="../images/option/order1.png" alt=""> -->
      </div>
      <div class="flex flex-wrap">
        <a class="social-content-card text" :href="item.link" v-for="item in renderLinks" :key="item">
          <div class="flex jc-between ai-center">
            <div>{{item.title}}</div>
            <div>
              <img class="social-content-arrow" src="../images/option/order1.png" alt="">
            </div>
          </div>
          <p class="social-content-link gray">{{item.link}}</p>
        </a>
      </div>
    </div>
  </div>
</template>

<script>

import LocaleBar from "@/views/common/localeBarCom";
import {mapState, mapGetters} from "vuex";
import { socialLinks } from "@/common/config"

const telegramIcon = require('../images/social/bxl-telegram6@2x.png')
const twitterIcon = require('../images/social/Shape@2x.png')
const discordIcon = require('../images/social/discord@2x.png')
const instagramIcon = require('../images/social/instagram-fill@2x.png')
// const mediumIcon = require('../images/social/medium6@2x.png')
const redditIcon = require('../images/social/reddit@2x.png')
// const whatsappIcon = require('../images/social/whatsapp-fill@2x.png')
const facebookIcon = require('../images/social/facebook_facebook52@2x.png')
const youIcon = require('../images/social/You@2x.png')

export default {
  name: 'social',
  components: {
    LocaleBar,
  },
  computed: {
    ...mapState('app', ['locale']),
    ...mapGetters(['language']),
    renderCtx() {
      if (this.socials[this.active]) {
        return this.socials[this.active]
      }
      return {
        title: '',
        tips: '',
        links: {},
      }
    },
    renderLinks() {
      if (this.socials[this.active] && this.socials[this.active].links && this.socials[this.active].links[this.localVal]) {
        return this.socials[this.active].links[this.localVal]
      }
      return []
    },
  },
  data() {
    return {
      localVal: '',
      active: 'telegram',
      telegram: {
        name: 'telegram',
        icon: telegramIcon,
        title: 'Telegram',
      },
      
      list: [{
        name: 'twitter',
        icon: twitterIcon,
        title: 'Twitter',
        link: 'https://twitter.com/DEFX_EN',
      // }, {
      //   name: 'Website',
      //   icon: twitterIcon,
      //   title: 'Website',
      //   href: 'https://defx.vip/#/index',
      }, {
        name: 'instagram',
        icon: instagramIcon,
        title: 'Instagram',
        link: 'https://www.instagram.com/defxofficial/',
      }, {
        name: 'reddit',
        icon: redditIcon,
        title: 'Reddit',
        link: 'https://www.reddit.com/user/DEFX2021',
      }, {
        name: 'Discord',
        icon: discordIcon,
        title: 'discord',
        link: 'https://discord.gg/bx6QWa8s/',
      }, {
        name: 'facebook',
        icon: facebookIcon,
        title: 'Facebook',
        link: 'https://web.facebook.com/profile.php?id=100074030050336',
      }, {
        name: 'youtube',
        icon: youIcon,
        title: 'Youtube',
        link: 'https://www.youtube.com/channel/UC0UQZIxykIppRibGHr6Opvg',
      }],
      socials: {
        telegram: {
          title: 'social_title1',
          tips: 'social_tip2',
          links: socialLinks.telegram
        }
      }
    };
  },
  beforeMount() {
    if (this.locale !== 'en') {
      this.localVal = 'all'
    } else {
      this.localVal = this.locale
    }
  },
  methods:{
    changeHandle(key) {
      this.localVal = key
    },
  }
};
</script>

