<template>
  <div class="locale-bar flex ai-center" :class="{show}" @click.stop="show=!show">

    <img class="icon-s animate-inout" src="../../images/lang@2x.png" alt="">
    <m-button class="btn" :height="30">
      {{ countries[showLocale].text }}
    </m-button>
    <img class="locale-bar-arrow animate-inout" src="../../images/option/order1.png" alt="">
    <ul class="shadow-active">
      <li v-for="(f,key) in countries" :class="{active:key===showLocale}" @click="changeHandle(key)" :key="key">
        {{ f.text }}
      </li>
    </ul>
  </div>
</template>

<script>
import {mapActions, mapState} from "vuex";
import MButton from "@/components/mButton";

export default {
  name: "localeBar",
  components: {MButton},
  computed: {
    ...mapState('app', ['locale']),
    countries() {
      const data = {
        'en': {text: 'English'},
        'kr': {text: '한국인'},
        'ja': {text: '日本語'},
        'es': {text: 'Español'},
        'fr': {text: 'French'},
        'de_DE': {text: 'Deutsch'},
        'ar_AE': {text: 'العَرَبِيَّة'},
        'zh_CN': {text: '简体中文'},
        'zh_TW': {text: '繁体中文'},
      }
      if (this.hasAll) {
        return {
        'all': {text: 'All'},
          ...data,
        }
      }
      return data
    },
    showLocale() {
      if (this.isStore) return this.locale
      return this.value || this.locale
    }
  },
  props: {
    isStore: Boolean,
    hasAll: Boolean,
    value: String,
  },
  data() {
    return {
      // countries: {
      //   'en': {text: 'English'},
      //   'kr': {text: '한국인'},
      //   'ja': {text: '日本語'},
      //   'es': {text: 'Español'},
      //   'fr': {text: 'French'},
      //   'de_DE': {text: 'Deutsch'},
      //   'ar_AE': {text: 'العَرَبِيَّة'},
      //   'zh_CN': {text: '简体中文'},
      //   'zh_TW': {text: '繁体中文'},
      // },
      show: false,
    }
  },
  methods: {
    ...mapActions('app', ['setLocale']),
    outerClick(){
      if (this.show) this.show = false;
    },
    changeHandle(key) {
      if (this.isStore) {
        this.setLocale(key)
      } else {
        this.$emit('change', key)
      }
    }
  },
  beforeMount() {
    window.addEventListener('click',this.outerClick);
  },
  unmounted() {
    window.removeEventListener('click',this.outerClick);
  }
}
</script>

