  height: 50px;
  position: relative;
  background: #292D34;
  box-shadow: 0px 1px 3px 0px rgba(16, 64, 54, 0.21);
  border-radius: 20px;
  padding: 0 20px;
  .flag {
    height: 33px;
    display: block;
  }
  .icon-s {
    margin-right: 5px;
    transform: rotate(0);
  }
  &-arrow{
    transform: rotate(180deg);
    width: 40px;
  }
}
.btn {
  white-space: nowrap;
  width: 80px;
  margin: 0 8px;
  border: none;
}
&.show {
  ul {
    display: block;
  }
  .icon-s {
    transform: rotate(180deg);
  }
}
ul {
  border-radius: 5px;
  position: absolute;
  bottom: calc(100% + 10px);
  right: 0;
  background-color: #222a3d;
  display: none;
}
li {
  padding: 0 15px;
  line-height: 30px;
  cursor: pointer;
  white-space: nowrap;
  &:hover {
    background-color: #353B4D;
  }
  &.active {
    color: #FFFFFF;
    background-color: #464d65;
  }
  &:first-child{
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
  }
  &:last-child{
    border-bottom-left-radius: 5px;
    border-bottom-right-radius: 5px;
  }
}
@media screen and (max-width: 768px) {
  .locale-bar {
    .flag {
      height: 25px;
    }
  }
}
</style>